import { Component} from '@angular/core';
declare var $ : any;
@Component({   
    moduleId:module.id,
   	selector: 'hoops-header',
   	templateUrl: './header.component.html',
})


export class HeaderComponent {

    public opensearch = false;

    onClickedOutside(e: Event) {

        this.opensearch = false;

    }

}




